        }

        @include media( '>=desktop' ) {
            font-size: calc( #{$fontSizeBaseSubheadline} / #{map-get( $colsByBreakpoint, desktop )} );
        }
    }

    &__hero-text {
        font-size: 1.5rem;
    }
